@import "../../sass/styles.scss";

.about {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    @media only screen and (max-width:480px) {
        grid-template-columns : none;
        grid-template-rows: repeat(2);
    }
    h1,h3 {
        font-weight: lighter;
    }
    h3 {
        font-size: 1.4rem;
        margin-bottom: 1rem;
        margin-top: 4rem;
    }
    .block {
        display: block;
        height: 90vh;
        padding: 0.2rem;
        @media only screen and (max-width:480px) {
            height: 50vh ;
        }
    }
    .my-image {
        background: url("../../assets/images/about/about.webp") no-repeat;
        background-size: cover;
        height: 100% ;
        @media only screen and (max-width:480px) {
            height: 50vh ;
        }
    }
    .my-info {
        padding: 12vh 10vw;
        height: initial;
    }
}
