@import "../../sass/styles.scss";

footer {
    text-align: center;
    margin: 2rem 4rem;
    color: $font-color;
    @media only screen and (max-width:480px) {
        padding: 0;
        margin: 2rem;
    }
    img {
        padding: 0rem 1rem;
    }
    a {
        color: $font-color;
        font-family: cinzel, serif;
        font-weight: bold;
        text-decoration: none;
        font-size: 1.1rem;
    }
}