@import "../../sass/styles.scss";

.background {
    background: url("../../assets/images/home/background_1.webp") no-repeat;
    background-size: cover;
    height: 100vh;
    text-align: center;
    color: white;
    display: block;
    h1 {
        padding-top: 35vh;
        font-size: 4rem;
        font-weight: lighter;
        font-family: cinzel, serif;
        margin: 0;
    }
    p {
        font-size: 1.4rem;
        letter-spacing: 0.5rem;
    }
    button {
        background: transparent;
        border: 1px solid white;
        color: white;
        padding: .8rem 2.5rem;
        font-size: 1rem;
        margin-top: 1rem;
    }
}
