@import "../../sass/styles.scss";

.blogpost-container {
    display: grid;
    grid-template-columns: 70% 30%;
    margin: 2rem auto;
    max-width: 60vw;
    @media only screen and (max-width: 1080px) {
        grid-template-columns: none;
        padding: 0 12vw;
    }
    @media only screen and (max-width: 480px) {
        padding: 0;
        max-width: 85vw;
    }

    .blogpost {
        margin-bottom: 4rem;
        // @media only screen and (max-width: 1080px) {
        //     display: none;
        // }
        button {
            background: transparent;
            border: 1px solid $font-color;
            color: $font-color;
            padding: 0.8rem 2rem;
            font-size: 1rem;
            // margin-top: 1rem;
        }
        img,
        p {
            max-width: 95%;
        }
        h4 {
            margin-bottom: 0.4rem;
            font-size: 1.4rem;
            @media only screen and (min-width: 780px) {
                margin-top: 4rem;
            }
        }
        h6 {
            margin: 0 0 0.6rem 0;
            font-size: 0.9rem;
        }
    }
    .recentposts {
        margin-top: 4rem;
        margin-left: 4rem;
        @media only screen and (max-width: 1080px) {
            display: none;
        }
        // hr {
        //     margin: 0;
        // }
        h4 {
            margin-bottom: 0.7rem;
            font-size: 1.2rem;
        }
        h5 {
            margin: 1.2rem 0 0.2rem 0;
            font-size: 1rem;
        }
        h6 {
            margin: 0 0 0rem 0;
            font-size: 0.8rem;
        }
    }
}
