@import "../../sass/styles.scss";

.portfolio {
    color: white;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    @media only screen and (max-width:480px) {
        grid-template-columns : none;
        grid-template-rows: repeat(3, 1fr);
    }
    h1 {
        padding-top: 30vh;
        font-size: 2.5rem;
        font-weight: lighter;
        font-family: cinzel, serif;
        margin: 0;
    }
    button {
        background: transparent;
        border: 1px solid white;
        color: white;
        padding: 0.8rem 2.5rem;
        font-size: 1rem;
        margin-top: 1.5rem;
    }
    .block {
        height: 90vh;
        margin: 0 0.3rem;
        text-align: center;
    }
    .editorial {
        background: url("../../assets/images/portfolio/editorial.webp")
            no-repeat;
    }
    .food-service {
        background: url("../../assets/images/portfolio/food-service.webp")
            no-repeat;
    }
    .baked-goods {
        background: url("../../assets/images/portfolio/baked-goods.webp")
            no-repeat;
    }
}
